<template>
  <div class="personal-content-body">
    <div class="content">
      <div class="content-title clear">
        <div class="left">项目管理</div>
        <el-input placeholder="搜索" v-model="keyWord" class="header-right-time right" :clearable='true' @change="getListData(1)">
          <i slot="prefix" class="el-input__icon el-icon-search "></i>
        </el-input>
      </div>
      <div class="historyapply-group">
        <div class="clear table-group-title">
          <div class="left breadcrumb">
            <el-breadcrumb separator-class="el-icon-arrow-right">
              <el-breadcrumb-item>项目总数（{{total}}）</el-breadcrumb-item>
            </el-breadcrumb>
          </div>
          <div class="right">
            <div class="table-group-title-btn btn-blue" @click="AddPriject">
              <span class="iconfont icon-add"></span>添加新项目
            </div>
          </div>
          <div class="right">
            <label for="upload-project-input">
              <div class="table-group-title-btn btn-blue">
                <span class="iconfont icon-daochu2"></span>导入项目信息
              </div>
            </label>
            <input type="file" ref="inputer" id="upload-project-input" style="display:none" v-on:change="uploadProjectExcel($event)" />
          </div>
          <div class="right">
            <div class="table-group-title-btn btn-blue" @click="toDownloadCusExcel">
              <span class="iconfont icon-daoru"></span>下载项目信息模版
            </div>
          </div>
          <div class="line"></div>
        </div>
        <div class="historyapply-group-tablebox">
          <div class="table-normal-box">
            <table class="table-normal">
              <tr>
                <th>序号</th>
                <th>项目名称</th>
                <th>管理人</th>
                <th>项目状态</th>
                <th>操作</th>
              </tr>
              <tr v-for="(item,index) in list" :key="index">
                <td>{{pageSize*(currentPage-1)+index+1}}</td>
                <td>{{item.name}}</td>
                <td>{{item.username}}</td>
                <!-- <td v-if="item.type==1">立项</td>
                    <td v-if="item.type==2">已投</td>
                    <td v-if="item.type==4">储备</td>
                    <td v-if="item.type==5">部分退出</td>
                    <td v-if="item.type==6">调研</td>
                    <td v-if="item.type==7">全部退出</td> -->
                <td>{{item.type_name==undefined?'':item.type_name}}</td>
                <td>
                  <span class="table-link" @click="goTogsManage(item.id,item.name,item.type)">管理</span>
                  <span class="table-link" @click="goToDelete(item.id,1,list.length)">删除</span>
                </td>
              </tr>
            </table>
            <div class="table-nodata" v-if="total==0">
              <img src="../../images/table_nodata.png"> 没有对应数据
            </div>
          </div>
          <div class="pagination-normal" v-if="total!=0">
            <el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="currentChange" :current-page="currentPage">
            </el-pagination>
          </div>
        </div>
      </div>
      <el-dialog :visible.sync="addaccountDialogVisible1" width="680px" center :append-to-body='true' :before-close="toCancleConfirm1" class="zxMoney_alert  project_alert">
        <div class="dialog_content1">
          <h3>您的基础版账号<span>项目管理数量已达上限</span>,如需增加请购买<span>项目管理扩容包</span></h3>
          <h2>付费套餐</h2>
        </div>
        <div class="dialog_content_info">
          <div class="clear">
            <div class="dialog_content_item left" v-for="(item,index) of confirmData.list" :key='index'>
              <span></span>
              <span>{{item.name}}</span>
              <span>{{item.price}}</span>
            </div>
          </div>
        </div>
        <div class="dialog_content_bottom">{{confirmData.tel}}</div>
        <div class="dialog_content_bottom1">具体详情请联系我们进行咨询</div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import {
  projectLsit,
  addProjectExcel,
  downloadaddProjectExcel,
  deleteProjectAddFund,
  fileTemplate
} from '../../config/api'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      list: [],
      pageSize: 10,
      selectTime: null,
      searchType: null,
      keyWord: null,
      total: null,
      currentPage: 1,
      name: null,
      phone: null,
      addaccountDialogVisible1: false, //付费弹窗
      confirmData: {
        // general: {},
        list: [],
      },
    }
  },
  mounted() {
  	var page = 1;
  	if (this.$route.query.page) {
  		page = parseInt(this.$route.query.page);
  	}
    this.getListData(page);
  },
  methods: {
    //获取数据
    currentChange(val) {
      if (this.total > this.pageSize) {
        this.getListData(val)
      }
    },
    getListData(val) {
      this.currentPage = val;
      this.list = [];
      let params = {
        page: val,
        number: this.pageSize,
      }
      if (this.keyWord != null && this.keyWord != '') {
        params.search = this.keyWord;
      }
      projectLsit(params).then(res => {
        // console.log(res);
        let data = res.data.payload;
        this.total = data.total;
        if (data.data.length != 0) {
          this.list = data.data;
          this.name = data.name;
          this.phone = data.phone;
        }
      })
    },
    goToProHome(id, name) {
      let params = {
        id: id,
      }
      this.$router.push({
        path: '/home/investProjectManageHome',
        query: params
      })
    },
    goTogsManage(id, name, type) {
      let params = {
        id: id,
        page: this.currentPage
      }
      this.$router.push({
        path: '/home/investProjectManageHomepage',
        query: params
      })
    },
    //导入项目信息文件
    uploadProjectExcel(e) {
      let inputDOM = this.$refs.inputer;
      // 通过DOM取文件数据
      let file = inputDOM.files[0];
      if (file == undefined) {
        return false;
      }
      // console.log(file);
      // console.log(file.name);
      if (!/\.xl(s[xmb]|t[xm]|am|s)$/.test(file.name)) {
        dialogConfirm('info', '请选择Excel格式文件')
        inputDOM.value = '';
        return false
      }
      addProjectExcel({}, file).then(res => {
        if (res.data.msgNo == 1) {
          dialogConfirm('success', '导入成功');
          this.currentPage = 1;
          inputDOM.value = '';
          this.getListData(1);
        } else if (res.data.msgNo == 9527) {
          inputDOM.value = '';
          this.confirmData = res.data.payload[0];
          this.addaccountDialogVisible1 = true;
        } else {
          inputDOM.value = '';
          dialogConfirm('info', res.data.message);
        }
      })
    },
    //下载模板
    toDownloadCusExcel() {
      fileTemplate({
        fileName:'ytxmqkb'
      }).then(res => {
        if (res.data.msgNo == 1) {
          window.location.href = res.data.payload;
        } else {
          dialogConfirm('info', res.data.message)
        }
      });
    },
    toCancleConfirm1() {
      this.addaccountDialogVisible1 = false;
    },
    AddPriject() {
      this.$router.push({
        name: 'newProjectProcess',
        query: {
          type: 1,
        }
      });
    },
    goToDelete(id, type,length) {
      dialogConfirmHasbtn('确定要删除该项目？').then(() => {
        deleteProjectAddFund({
          type: type,
          id: id,
        }).then(res => {
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '删除成功');
            if(length>1){
              this.getListData(this.currentPage);
            }else{
              if(this.currentPage>1){
                this.getListData(this.currentPage-1);
              }else{
                this.getListData(this.currentPage);
              }
            }
            
          } else {
            dialogConfirm('info', res.data.message)
          }
        });
      }).catch(() => {});
    },

  }
}

</script>
<style lang="scss" scoped>
.content {
  width: 100%;
  min-height: 100%;
  min-width: 1000px;
  background-color: white;
  padding: 36px 40px;
}

.histroyapply-icon {
  width: 15px;
  height: 15px;
  background: url('../../images/histroyapply_icon.png') no-repeat;
  display: inline-block;
  float: left;
  margin: 2px 6px 0px 0px;
}

.historyapply-group {
  width: 100%;
  border: 1px solid #E4E4E4;
  padding: 18px 0 16px;
}

.historyapply-group-total {
  font-size: 18px;
  color: #3C98E8;
  font-weight: bold;
  line-height: 1em;
  margin-bottom: 35px;
}

.historyapply-group-tablebox {
  // padding: 0px 18px 0px 14px;
  padding: 0 50px;
  min-height: 440px;
}

.table-normal-box {
  min-height: 440px;
}

.pagination-normal {
  text-align: center;
  margin-top: 20px;
}

.header-right-time {
  // margin-right: 10px;
  position: relative;
}

.cz {
  color: #3C98E8;
  cursor: pointer;
}

.table-group-title-btn {
  &>span {
    display: inline-block;
    width: 20px;
    height: 20px;
  }
  .icon_upload {
    background: url('../../images/icon_upload.png') no-repeat;
    background-size: 90%;
  }
  .icon_download {
    background: url('../../images/icon_download.png') no-repeat;
    background-size: 100%;
    margin-top: 4px;
  }
}

.dialog_content1 {
  h3 {
    text-align: center;
    margin-top: 70px;
    font-size: 16px;
    span {
      color: #3C98E8;
    }
  }
  h2 {
    text-align: center;
    color: #4A90E2;
    font-size: 16px;
    margin-top: 18px;
    font-weight: bold;
  }
}

.dialog_content_info {
  text-align: center;
  &>div {
    width: 465px;
    height: 85px; // background-color: red;
    padding-top: 10px;
    display: inline-block;
    position: relative;
    left: 3px;
    .dialog_content_item {
      width: 50%;
      padding: 0 20px;
      margin-bottom: 10px;
      display: flex;
      align-items: center;
      &>span:nth-child(1) {
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background-color: #3C98E8;
      }
      &>span:nth-child(2) {
        font-size: 14px;
        color: #3C98E8;
        width: calc(100% - 66px);
        text-align: left;
        padding-left: 8px;
      }
      &>span:nth-child(3) {
        width: 60px;
        height: 24px;
        line-height: 24px;
        background: url('../../images/icon_back.png') no-repeat center;
        background-size: 100%;
        background-position-y: -2px;
        font-size: 12px;
        color: white;
      }
    }
  }
}

.dialog_content_bottom {
  font-size: 20px;
  color: #fff;
  position: absolute;
  bottom: 47px;
  width: calc(100% - 17px);
  height: 20px;
  text-align: center;
}

.dialog_content_bottom1 {
  font-size: 12px;
  color: #A0A4AA;
  position: absolute;
  bottom: 93px;
  width: calc(100% - 50px);
  height: 20px;
  text-align: center;
}

</style>



